import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const MIN = 20;
const MAX = 60;

const initialState = {
	productionData: wutil.makeRandomData(500, MIN, MAX),
	wearData: wutil.makeRandomData(500, MIN, MAX),
	windData: wutil.makeRandomData(500, MIN, MAX),
	priceData: wutil.makeRandomData(500, MIN, MAX),
	demandData: wutil.makeRandomData(500, MIN, MAX),
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();	
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
        appState.getFingerprint();
        await userStore.getInfo();
    }

	render() {
		const { productionData, wearData, windData, priceData, demandData } = this.state;
		// const { userStore } = this.props.stores;
		// const { user } = userStore;
		return (
			<div class='container-fluid mt-5 mb-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Home</h1>

						<div class='d-flex'>
							<div class='p-2 flex-grow-1 bg-light mr-2'>
								<i class='fas fa-chart-bar' /> Production / <i class='fas fa-thermometer' /> Wear
								<LineGraph 
									data={productionData}
									data2={wearData}
									width={1200}
									height={200}
									yMax={100}
									y2Max={100}
									showYTicks={1}
									yTicks='["100%", "75%", "50%", "25%", "0%"]'
									showXTicks={1}
								/>
							</div>
							{/* <div class='p-2 bg-light'>
								<i class='fas fa-battery-three-quarters' /> Wear status
							</div> */}
						</div>

						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light mr-2'>
								<i class='fas fa-cloud-sun-rain' /> Weather
								<LineGraph 
									data={windData}
									width={1200}
									height={200}
									yMax={100}
									showYTicks={1}
									yTicks='["20 m/s", "15 m/s", "10 m/s", "5 m/s", "0 m/s"]'
									showXTicks={1}
								/>
							</div>
						</div>
						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light mr-2'>
								<i class='fas fa-chart-line' /> Electricity prices
								<LineGraph 
									data={priceData}
									width={1200}
									height={200}
									yMax={100}
									showYTicks={1}
									yTicks='["2 NOK", "1.5 NOK", "1 NOK", "0.5 NOK", "0 NOK"]'
									showXTicks={1}
								/>
							</div>
						</div>
						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light'>
								<i class='fas fa-hand-holding-usd' /> Demand
								<LineGraph 
									data={demandData}
									width={1200}
									height={200}
									yMax={100}
									showYTicks={1}
									yTicks='["100 MWh", "75 MWh", "50 MWh", "25 MWh", "0 MWh"]'
									showXTicks={1}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
