
class WifetUtil {
    static getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    static makeRandomData(cnt = 200, min, max, onlyUp = false) {
        const data = [];
        let current = 0;
        for (let i = 0, l = cnt; i < l; i += 1) {
            if (!current) {
                current = WifetUtil.getRandomInt(min, max);
            } else if (onlyUp) {
                current = WifetUtil.getRandomInt(current, current + 3);
            } else {
                current = WifetUtil.getRandomInt(current - 5, current + 5);
            }
            data.push({ x: i, y: current })
        }
        return data;
    }
}

export default WifetUtil;
